import React from 'react'
import {Card} from "antd"
import TheCharts from 'echarts/lib/echarts'
import Theme from "./echartTheme"
import ForReact from "echarts-for-react"
import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'

class Charts extends React.Component {
  render() {
    return (
      <div style={{margin: "20px auto", width: "1010px"}}>
        <Card title="柱形图">
          <ForReact option={this.getOptionOne()} theme="beyond" style={{height: "300px"}}/>
        </Card>

        <Card title="柱形图二" style={{marginTop: "20px"}}>
          <ForReact option={this.getOptionTwo()} theme="beyond" style={{height: "300px"}}/>
        </Card>

        <Card title="饼形图" style={{marginTop: "20px"}}>
          <ForReact option={this.getOptionThree()} theme="beyond" style={{height: "300px"}}/>
        </Card>

        <Card title="饼形图" style={{marginTop: "20px"}}>
          <ForReact option={this.getOptionFour()} theme="beyond" style={{height: "300px"}}/>
        </Card>
      </div>
    )
  }


  componentWillMount() {
    TheCharts.registerTheme("beyond", Theme)
  }

  getOptionOne = () => {
    const option = {
      title: {
        text: "本周下载量"
      },
      tooltip: {
        trigger: "axis"
      },
      xAxis: {
        data: ['周一', "周二", "周三", "周四", "周五", "周六", "周日"]

      },
      yAxis: {
        type: "value"
      },
      series: [
        {
          name: "下载量",
          type: "bar",
          data: [100, 200, 300, 500, 1100, 500, 900]
        }
      ]
    };
    return option;
  }

  getOptionTwo = () => {
    const option = {
      title: {
        text: "本周对比"
      },
      legend: {
        data: ["下载量", "浏览量"]
      },
      tooltip: {
        trigger: "axis"
      },
      xAxis: {
        data: ['周一', "周二", "周三", "周四", "周五", "周六", "周日"]

      },
      yAxis: {
        type: "value"
      },
      series: [
        {
          name: "下载量",
          type: "bar",
          data: [100, 200, 300, 500, 1100, 500, 900]
        },
        {
          name: "浏览量",
          type: "bar",
          data: [60, 180, 260, 420, 700, 360, 690]
        }
      ]
    };
    return option;
  };

  getOptionThree = () => {
    const option = {
      title: {
        text: "本周下载",
        x: "left",
      },
      legend: {
        orient: "vertical",
        right: 10,
        top: 20,
        bottom: 20,
        data: ['周一', "周二", "周三", "周四", "周五", "周六", "周日"]
      },
      tooltip: {
        trigger: "item",
        formatter: '{a}<br/>{b}: {c} ({d}%)'
      },
      series: [
        {
          name: "下载量",
          type: "pie",
          x: "bottom",
          data: [
            {
              name: "周一",
              value: 1000,
            },
            {
              name: "周二",
              value: 2000,
            },
            {
              name: "周三",
              value: 3000,
            },
            {
              name: "周四",
              value: 4000,
            },
            {
              name: "周五",
              value: 5000,
            },
            {
              name: "周六",
              value: 6000,
            },
            {
              name: "周日",
              value: 7000,
            }
          ]
        }
      ]

    };
    return option;
  };

  getOptionFour = () => {
    const option = {
      title: {
        text: "本周下载",
        x: "left",
      },
      legend: {
        orient: "vertical",
        right: 10,
        top: 20,
        bottom: 20,
        data: ['周一', "周二", "周三", "周四", "周五", "周六", "周日"]
      },
      tooltip: {
        trigger: "item",
        formatter: '{a}<br/>{b}: {c} ({d}%)'
      },
      series: [
        {
          name: "下载量",
          type: "pie",
          radius:["50%","70%"],
          center:["50%","60%"],
          data: [
            {
              name: "周一",
              value: 1000,
            },
            {
              name: "周二",
              value: 2000,
            },
            {
              name: "周三",
              value: 3000,
            },
            {
              name: "周四",
              value: 4000,
            },
            {
              name: "周五",
              value: 5000,
            },
            {
              name: "周六",
              value: 6000,
            },
            {
              name: "周日",
              value: 7000,
            }
          ]
        }
      ]
    };
    return option;
  };
}

export default Charts;
