import React, {Fragment} from 'react';
import {Redirect} from 'react-router-dom';
import {Form, Card, Input, Button} from "antd";
import {actionCreators} from "./store";
import {connect} from "react-redux";

const FormItem = Form.Item;

class FormLogin extends React.Component {
  render() {
    const {loginStatus} = this.props;
    if (!loginStatus) {
      return (
        <Fragment>
          <div style={{width: "300px", margin: "80px auto"}}>
            <Card title="登录">
              <Form>
                <FormItem>
                  <Input placeholder={"username"} ref={(input) => {
                    this.account = input
                  }}/>
                </FormItem>
                <FormItem>
                  <Input placeholder={"password"} ref={(input) => {
                    this.password = input
                  }}/>
                </FormItem>
                <FormItem>
                  <Button onClick={() => this.props.login(this.account, this.password)}>
                    登录
                  </Button>
                </FormItem>
              </Form>
            </Card>
          </div>
        </Fragment>
      )
    } else {
      return <Redirect to='/'/>
    }
  }

}

const mapState = (state) => ({
  loginStatus: state.getIn(['login', 'login'])
});

const mapDispatch = (dispatch) => ({
  login(accountElem, passwordElem) {
    dispatch(actionCreators.login(accountElem.state.value, passwordElem.state.value))
  }
});

export default connect(mapState, mapDispatch)(FormLogin);
